import React, { useState, useEffect } from 'react';
import Wrapper from '../common/Wrapper';
import { Link, withRouter } from 'react-router-dom';
import { makeStyles, rgbToHex } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import quiz from '../../pages/contents/question';
import score from '../../pages/contents/score';
import Button from '../common/Button';
import styled from 'styled-components';
import '../common/Main.css';
import { ArrowBack } from '@material-ui/icons';

const Question = styled.div`
  text-align : center;
  margin : 0;
  padding : 0;
`;

const Spacer = styled.div`
  height : 4rem;
`;

const useStyles = makeStyles({
    title: {
      fontSize: 21,
      marginBottom : '10px',
      marginTop : '10px',
      textAlign: "center",
      fontFamily: 'IBMPlexSansKR',
      fontWeight: 900,
      color : 'white'
    },
    button: {
      textAlign: "center",
      fontFamily: 'IBMPlexSansKR-Regular',
      marginTop: '5%',
      backgroundColor: '#fff',
      '@media (min-width: 780px)': {
        '&:hover': {
          backgroundColor: '#407aff',
          color: 'white',
        },
      }
    },
  });


const QuizCard = ({ match }) => {
    const classes = useStyles();
    const [curQuiz, setQuiz] = useState({});
    const [curScore, setScore] = useState(score);
    const [valueArray, setValueArray] = useState([]);
    const [id, setId] = useState(0);

    useEffect(()=>{
      const num = parseInt(match.params.id);
      if(quiz){
        setQuiz(quiz[num-1]);    //렌더링 시 질문 설정 (현재 주소 파라미터에서 질문 번호 가져온다.
        setId(num+1);
      }
    }, [match]);

    const addScore = (value) => {
    //score의 type 이랑 answer의 타입을 같은 것을 찾기
    //score에게 answer의 value 만큼 더해주기
      valueArray.push(value)
      console.log(valueArray)
      setValueArray(valueArray);
    };

    const sumScore = (value) => {
      valueArray.push(value)
      for(let i = 0; i < valueArray.length; i++){
        if(i < 9){
          curScore.depression += valueArray[i];
        } else if(i < 16){
          curScore.anxiety += valueArray[i];
        } else if(i < 26){
          curScore.esteem += valueArray[i];
        } else if(i < 36){
          curScore.resilience += valueArray[i];
        } else {
          curScore.icq += valueArray[i];
        }
      }
      setValueArray(valueArray);
      setScore(curScore);
    }
    const goBack = () => {
      valueArray.pop();
      console.log(valueArray);
      setValueArray(valueArray);
    }

    return(
        <Wrapper>
          {match.params.id < 51 &&
            <Question>
                <Typography className={classes.title}>{match.params.id}/51</Typography><br/>
                <Typography className={classes.title}>{curQuiz.question}</Typography>
                <br/>
                
                {curQuiz.answer && curQuiz.answer.map((item, index)=>(
                 <Link to={`/question/${id}`} key={index} >
                    <Button className={classes.button} 
                    weight={"normal"} width={"85%"} fontSize={"1.1em"} color={"black"} onClick={() => addScore(item.value)}>{item.text} </Button> 
                 </Link>
                ))}
                {match.params.id > 1 && 
                  <Link to={`/question/${id - 2}`} key={5} >
                    <Button className={classes.button} 
                    weight={"normal"} width={"20%"} fontSize={"1.1em"} style={{backgroundColor: '#407aff'}} color={"white"} onClick={() => goBack()}>
                      이전
                    </Button> 
                      {/* <ArrowBack className={classes.button} style={{width: '30%', backgroundColor: '407aff', color:'white', padding: '10px 0px', borderRadius: '10px'}} onClick={()=> goBack()}/> */}
                  </Link>
                }
            </Question>
          }
          {match.params.id == 51 &&
            <Question>
                <Typography className={classes.title}>{match.params.id}/51</Typography><br/>
                <Typography className={classes.title}>{curQuiz.question}</Typography>
                <br/>
                {curQuiz.answer && curQuiz.answer.map((item, index)=>(
                 <Link to={{pathname:`/result`, state: score}} key={index}>
                    <Button className={classes.button}
                    weight={"normal"} width={"85%"} fontSize={"1.1em"} color={"black"} 
                    onClick={() => sumScore(item.value)}>{item.text}</Button> 
                 </Link>
                ))}
                <Link to={`/question/${id - 2}`} key={5} >
                    <Button className={classes.button} 
                    weight={"normal"} width={"30%"} fontSize={"1.1em"} color={"black"} onClick={() => goBack()}> 뒤로가기 </Button> 
                </Link>
            </Question>
          }
        </Wrapper>
    );
};

export default withRouter(QuizCard);